.nav__list {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  white-space: nowrap;
  -ms-overflow-style: none;
  scrollbar-width: none;
  gap: 5px; }
  @media (min-width: 1440px) {
    .nav__list {
      flex-wrap: wrap; } }

.nav__link,
.nav__link.text:active,
.nav__link.text:hover {
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #7e7e82;
  padding: 8px 16px; }

.nav {
  padding-left: 12px; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .nav {
      padding-left: 24px; } }
  @media (min-width: 1440px) {
    .nav {
      padding-left: 33px; } }

.nav__item {
  display: flex;
  border: 2px solid #f8f8f8;
  border-radius: 6px; }

.nav__item:hover {
  border: 2px solid #b8ffec;
  border-radius: 6px;
  background-color: white;
  cursor: pointer; }

.nav__list::-webkit-scrollbar {
  display: none; }

.button {
  display: flex;
  align-items: center; }

.nav-header__item {
  display: flex;
  align-items: center; }

.nav-header__sublist {
  display: flex;
  align-items: center;
  gap: 16px; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .nav-header__sublist {
      gap: 16px; } }

.header-desktop {
  display: flex;
  flex-direction: column;
  gap: 42px; }
  @media (min-width: 1440px) {
    .header-desktop {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      background-color: #f8f8f8;
      padding-top: 42px; } }

.nav-header__logo {
  width: 98px;
  height: 52px;
  display: flex; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .nav-header__logo {
      padding-left: 22px; } }

.nav-header__sublist .button:nth-child(n + 2):nth-child(-n + 4) {
  display: none; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .nav-header__sublist .button:nth-child(n + 2):nth-child(-n + 4) {
      display: flex; } }

@media (min-width: 768px) and (max-width: 1440px) {
  .nav-header__sublist .button:nth-child(3) {
    padding-right: 22px; } }

.nav-header__sublist .button:nth-child(5)::before {
  content: ' ';
  position: relative;
  display: block;
  width: 2px;
  height: 32px;
  border-right: 2px solid #eaeaea;
  left: -75px; }

@media (min-width: 768px) and (max-width: 1440px) {
  .nav-header__sublist .nav-header__logo::after {
    content: ' ';
    position: relative;
    display: block;
    width: 2px;
    height: 32px;
    border-right: 2px solid #eaeaea;
    right: 115px;
    top: 10px; } }

.nav-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  width: 100%;
  padding-left: 16px;
  padding-right: 16px; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .nav-header {
      justify-content: space-between;
      padding-right: 24px;
      padding-left: 24px; } }
