.header {
  display: flex;
  flex-direction: column;
  gap: 20px; }

.header-container {
  background-color: white;
  height: 88px;
  display: flex;
  border-bottom: 1px solid #d9fff5; }
  @media (min-width: 1440px) {
    .header-container {
      display: none; } }

@media (min-width: 768px) and (max-width: 1440px) {
  .header__logo::before {
    left: -24px; } }

.header__heading {
  display: flex;
  font-size: 28px;
  letter-spacing: -0.6px;
  font-family: 'TT Lakes', 'Arial', sans-serif;
  font-weight: 700;
  padding-left: 10px; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .header__heading {
      padding-left: 20px; } }
  @media (min-width: 1440px) {
    .header__heading {
      position: absolute;
      padding-left: 33px;
      top: 42px; } }

.header__heading::before {
  content: ' ';
  position: relative;
  left: -10px;
  top: -2px;
  display: block;
  width: 0;
  height: 40px;
  border-right: 4px solid #41f6d7;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .header__heading::before {
      left: -20px; } }
  @media (min-width: 1440px) {
    .header__heading::before {
      left: -33px;
      border-radius: 5px; } }

.header__left-column {
  display: flex; }

.header__right-column {
  display: flex; }

.header__right-menu {
  display: flex;
  gap: 16px; }

.header-desktop__menu {
  display: none; }
  @media (min-width: 1440px) {
    .header-desktop__menu {
      display: flex;
      justify-content: flex-end;
      gap: 180px;
      padding-right: 32px;
      padding-top: 42px; } }

@media (min-width: 1440px) {
  .header-desktop__menu-text {
    position: absolute;
    left: -140px;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.2px;
    background-color: #f8f8f8;
    border-width: 0;
    font-family: 'TT Lakes', 'Arial', sans-serif;
    font-weight: 500; } }
