.button-order {
  width: 119px;
  height: 32px;
  background: #ff3e79;
  border-radius: 16px;
  color: white;
  font-size: 12px;
  line-height: 24px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  grid-column: 2;
  grid-row: 3;
  align-self: flex-end; }
  @media (min-width: 768px) {
    .button-order {
      grid-row: 2;
      grid-column: 4;
      justify-self: flex-end;
      font-size: 12px;
      line-height: 24px;
      letter-spacing: -0.15px; } }

.button-order:hover {
  opacity: 50%; }
