  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition: transform 500ms; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .swiper__container-wrapper {
      justify-content: space-between;
      flex-wrap: wrap;
      width: 100%;
      gap: 16px;
      height: 170px;
      overflow: hidden;
      transition: height 500ms; } }
  @media (min-width: 1440px) {
    .swiper__container-wrapper {
      justify-content: space-between;
      flex-wrap: wrap;
      width: auto;
      gap: 16px;
      height: 170px;
      overflow: hidden;
      transition: height 500ms; } }

@media (min-width: 768px) and (max-width: 1440px) {
  .swiper__container {
    width: auto;
