* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none; }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: 'TT Lakes', 'Arial', sans-serif;
  background-color: #f8f8f8; }

.container {
  display: flex;
  margin: 0 auto; }
  @media (min-width: 1440px) {
    .container {
      width: 1440px; } }

.container__main-content {
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  width: 320px;
  gap: 20px; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .container__main-content {
      width: 769px; } }
  @media (min-width: 1440px) {
    .container__main-content {
      width: 1440px;
      padding-left: 320px;
      margin: 0; } }

.container__main-content__blur {
  -webkit-filter: blur(2220px);
          filter: blur(2220px); }

.blurButton {
  position: fixed;
  width: 100vh;
  height: 100vh;
  z-index: 11;
  cursor: default; }

.blurButton:active {
  outline: none; }

button {
  cursor: pointer;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent; }

.main {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  gap: 16px; }
