.showmore-btn {
  font-family: 'TT Lakes', 'Arial', sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.2px;
  border-width: 0;
  background-color: shwMoreColor;
  display: block;
  position: relative;
  left: 16px; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .showmore-btn {
      left: 24px;
      top: -14px; } }
  @media (min-width: 1440px) {
    .showmore-btn {
      left: 32px;
      top: -14px; } }

@media (min-width: 768px) and (max-width: 1440px) {
  .content__text-box .showmore-btn {
    left: 24px; } }

@media (min-width: 1440px) {
  .content__text-box .showmore-btn {
    left: 24px; } }

.showmore-btn::before {
  content: ' ';
  position: relative;
  top: 17px;
  right: 24px;
  display: block;
  width: 8px;
  height: 11px;
  background-image: url(./static/arrows.svg);
  background-size: 8px 11px;
  background-repeat: no-repeat; }

.showmore-btn__hide::before {
  transform: rotate(180deg); }

.swiper__button {
  display: none; }
  @media (min-width: 768px) {
    .swiper__button {
      display: block; } }

@media (min-width: 1440px) {
  .mainCloseBtn {
    display: none; } }
