.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82;
  opacity: 0.72;
  padding-bottom: 19px;
  border-top: 1px solid #d9fff5; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .footer {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      padding: 24px; } }
  @media (min-width: 1440px) {
    .footer {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      padding: 31px; } }

.footer-text {
  text-align: center; }
  @media (min-width: 768px) and (max-width: 1440px) {
    .footer-text {
      text-align: left;
      width: 218px; } }
  @media (min-width: 1440px) {
    .footer-text {
      text-align: left;
      width: 330px; } }

.footer-text:nth-child(2) {
  text-align: center;
  color: #7e7e82; }

@media (min-width: 768px) {
  .footer-text:last-child {
    text-align: right; } }
